
@mixin showBorder() {
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 5px;
}

@mixin inlineWidth() {
    width: calc(100% - 60px);
}

.profile-body {
    position: relative;
    left: calc(249px + 40px);
    top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    max-width: calc(100% - 250px - 80px);
    height: 717px;
    border-radius: 25px;
    background-color: #FFFFFF;

    .profile-body-navigation {
        height: 66px;
        @include inlineWidth;
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        justify-content: flex-start;
        gap: 20px;
        .profile-body-navigation-item {
            height: 30px;
            display: flex;
            flex-direction: column;
            align-content: center;
            justify-content: flex-end;
            cursor: pointer;
            p {
                margin: 0 auto auto;
            }
            svg {
                opacity: 0;
                position: relative;
                bottom: 0;
            }
        }
        & #edit-profile {
            svg {
                opacity: 1;
            }
        }
    }

    .profile-body-content {
        @include inlineWidth;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-top: 53px;
        gap: 29px;

        .profile-body-picture-container {
            width: 130px;
            height: 130px;
            clip-path: circle(50% at 50% 50%);
            border-radius: 50%;
            cursor: pointer;
            img {
                width: auto;
                height: 130px;
            }
        }
        .profile-body-form {
            width: 100%;
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            justify-content: space-between;
            gap: 29px;
            .form-column {
                width: 100%;
                display: flex;
                flex-direction: column;
                gap: 22px;
                .form-element {
                    input {
                        width: calc(100% - 22px);
                    }
                }
            }
        }
    }

    hr {
        @include inlineWidth;
        margin: 0 0;
        color: rgba(244, 245, 247, 0.15);
    }

    .save-button {
        width: 190px;
        height: 50px;
        background-color: #1814F3;
        margin-top: 30px;
        margin-right: 30px;
        align-self: flex-end;
        border-radius: 15px;
        font-size: 18px;
        font-weight: 500;
        font-family: "Inter", sans-serif;
        color: white;
        border: none;
        cursor: pointer;
    }
}